.butt{
  background-color: orange !important;
  border-color: orange !important;
}

.slider{
  margin-top: -60px;
}
.nav{
  z-index: 100;
  background: radial-gradient(#000,transparent) ;
}
/* Extra large devices (large laptops and desktops, 1201px and up) */
@media only screen and (min-width: 1201px) {
  .centerText{
    margin: 80px auto 0px auto;
    text-align: center;
  }
  
  .firstheading h1{
    font-size: 400% !important;
    color: orange;
  }
  
  .para{
    width: 40%;
    text-align: center;
    margin: 45px;
  }
  
  .img{
    box-shadow: 0 5px 30px -5px rgba(0, 0, 0, 0.4);
    width: 40%;
    float: right;
    border-radius: 50%;
    margin: 5px 30px 0 30px;
    height: 200px;
  }
  
  #form{
    width: 90%;
    margin: auto;
  }
  
  .footCard{
    display: inline-block !important;
    width: 33% !important;
    text-align: center;
    background-color: black !important;
    color: white !important;
  }
  
  .footImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 10px;
  }
  
  .myHr{
      height: 5px;
      background: orange;
      width: 50%;
  }

  .product{
    width: 30%;
      display: inline-block;
      margin: 15px;
  }
  
  .product:hover .prodImg{
   opacity: .5;
   border-radius: 50%;
  }
  
  .serCard{
    width: 30%;
    display: inline-block !important;
    margin: 10px;
    background-color: orange !important;
  }
  
  .serCard:hover{
    top: -10px;
    zoom: 1.1;
  }
  
  .header a{
    color: white;
    margin-left: 70px;
  }
  
  .header a:hover{
    color: orange;
   text-decoration: none;
   font-weight: bolder;
  }

 
  
}

